<template>
  <div class="auth-wrapper pb-0">
    <div class="auth-content">
      <div class="card rounded">
        <div class="row align-items-center">
          <div class="col-md-12">
            <div class="card-body">
              <div class="text-center">
                <img src="@/assets/images/logo-unprg.png" alt="Logo" class="img-fluid mb-4">
                <h6 class="pb-3 text-secondary">Sistema de Gestión de Expedientes</h6>
                <span class="text-danger" v-show="showUserError">{{ userErrorMessage }}</span>
              </div>
              <form @submit.prevent="authenticateUser">
                <div class="form-group mb-3">
                  <label class="form-label text-left">Email</label>
                  <input ref="emailInput" type="text" class="form-control text-secondary" v-model="credenciales.login"
                    @input="validarCampo('login')" :class="{ 'has-error': errores.login }">
                  <span class="text-danger" v-if="errores.login">Ingrese un email.</span>
                </div>

                <div class="form-group mb-4">
                  <label class="form-label text-left">Contraseña</label>
                  <input type="password" class="form-control text-secondary" v-model="credenciales.password"
                    @input="validarCampo('password')" :class="{ 'has-error': errores.password }"
                    @keyup.enter="authenticateUser">
                  <span class="text-danger" v-if="errores.password">Ingrese una contraseña.</span>
                </div>

                <div class="text-center">
                  <button class="btn btn-lg btn-primary w-100 fw-bold rounded" type="submit">
                    <span v-if="!isLoading">Iniciar Sesión</span>
                    <span v-else class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
export default {
  name: 'Login',
  data() {
    return {
      credenciales: { login: "", password: "" },
      errores: { login: false, password: false },
      showUserError: false,
      userErrorMessage: '',
      isLoading: false
    };
  },
  created() {
    this.url = this.$store.getters.get__url;
  },
  mounted() {
    this.$refs.emailInput.focus();
  },
  methods: {
    validarCampo(campo) {
      this.errores[campo] = this.credenciales[campo] === "";
    },

    validarCampos() {
      Object.keys(this.errores).forEach(propiedadError => {
        this.validarCampo(propiedadError, propiedadError);
      });

      return !Object.values(this.errores).some(error => error);
    },

    async authenticateUser() {
      this.isLoading = true;
      if (this.validateFields()) {
        try {
          const response = await this.$axios.post(`${this.url}/user/login`, this.credenciales);

          if (response.data.state === 200) {
            this.handleSuccessfulLogin(response.data.user);
          } else {
            this.handleFailedLogin('Usuario o contraseña incorrectos.');
          }
        } catch (error) {
          this.handleFailedLogin(`Error al iniciar sesión: ${error.message}`);
        }
      }
      this.isLoading = false;
    },

    validateFields() {
      Object.keys(this.errores).forEach(field => {
        this.errores[field] = this.credenciales[field] === "";
      });

      return !Object.values(this.errores).some(error => error);
    },

    handleSuccessfulLogin(user) {
      console.log('Datos del usuario obtenidos:', user);
      this.showUserError = false;
      this.userErrorMessage = '';
      this.$store.commit('mt_set_user', user);
      //this.$router.push({ path: '/home' });
      if (user.usu_rol === 'ABOGADO' || user.usu_rol === 'ADMIN') {
      //   this.$store.commit('mt_set_aboid', user.abo_id);
      this.$router.push({ path: '/home' });
      } else {
      //    this.$store.commit('mt_set_assid', user.ass_id);
      this.$router.push({ path: '/panel' });
      }
    },

    handleFailedLogin(errorMessage) {
      this.showUserError = true;
      this.userErrorMessage = errorMessage;
    },
  },
};
</script>
  
<style scoped>
.has-error {
  border: 1px solid #EA4D4D;
}

.text-danger {
  font-size: 12px;
}
</style>